import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { Modal } from 'antd';
import { getMicroAppInfo, IMicroAppInfo } from '@/microApp';
import { IProps } from './type';
import MicroPanel from './microPanel';
import styles from './index.less';

// 外部visible拥有绝对控制权
export default function Popup(props: IProps): any {
  // 弹窗面板
  let { panel } = props;
  const {
    children, // 弹窗包裹容器
    width = 1024,
    visible: _visible,
    onCancel,
    needMaximize = false,
    needRetract = null,
    microAppKey,
    onRetract,
  } = props;
  let microAppInfo: IMicroAppInfo | null = null;
  if (microAppKey) {
    microAppInfo = getMicroAppInfo(microAppKey);
    panel = (options: any) => {
      return <MicroPanel microAppInfo={microAppInfo} />;
    };
  }
  if (!panel) {
    return children;
  }
  // 控制弹窗展示
  const [visible, setVisible] = useState(_visible || false);
  // 控制弹窗销毁
  const [isActive, setIsActive] = useState(_visible || false);
  // 控制弹窗最大化
  const [isMaximize, setIsMaximize] = useState(_visible || needMaximize);

  useEffect(() => {
    if (_visible !== undefined) {
      setVisible(_visible);
      if (_visible) {
        setIsActive(true);
      }
    }
  }, [_visible]);
  // 打开弹窗
  const onOpen = () => {
    setIsActive(true);
    setVisible(true);
  };
  // 关闭弹窗
  const onClose = () => {
    setVisible(false);
    if (onCancel) {
      onCancel();
    }
  };
  // 完全关闭后的回调
  const onAfterClose = () => setIsActive(false);
  const onLink = () => {
    window.open(microAppInfo?.entry);
  };

  // 渲染弹窗容器包含的孩子节点
  const renderChildren = () => {
    if (children) {
      return React.cloneElement(children as any, {
        onClick: onOpen,
      });
    }
    return undefined;
  };

  // 修改弹窗尺寸
  const onUpdateWindow = (type: 'maximize' | 'reduction') => {
    setIsMaximize(type === 'maximize');
  };

  // 渲染弹窗组件
  const renderModal = () => {
    return (
      <Modal
        open={_visible !== undefined ? _visible : visible}
        onCancel={onClose}
        afterClose={onAfterClose}
        destroyOnClose
        centered
        wrapClassName={classnames(styles['popup-model'], isMaximize && styles.maxWindow)}
        width={width}
        title={null}
        footer={null}
        closable={false}
      >
        <div className={classnames(styles.dialogPanel)}>
          {panel && panel({
            onClose,
          })}
        </div>
        <div className={classnames(styles.dialogTool)}>
          {microAppInfo?.entry && (
            <div className={classnames(styles.btn, styles.blue)} onClick={onLink}>
              <i className={styles.iconLink} />
            </div>
          )}
          {needRetract !== null && (
            <div className={classnames(styles.btn, styles.yellow)} onClick={onRetract || onClose}>
              <i className={styles.iconRetract} />
            </div>
          )}
          {needMaximize !== null && (
            <div
              className={classnames(styles.btn, styles.green)}
              onClick={() => onUpdateWindow(isMaximize ? 'reduction' : 'maximize')}
            >
              {isMaximize ? <i className={styles.iconCompressOut} /> : <i className={styles.iconExpandOut} />}
            </div>
          )}
          <div className={classnames(styles.btn, styles.red)} onClick={onClose}>
            <i className={styles.iconClose} />
          </div>
        </div>
      </Modal>
    );
  };

  return (
    <>
      {renderChildren()}
      {isActive && renderModal()}
    </>
  );
}
